export default {
	data() {
		return {
			user: "",
			psw: ""
		}
	},
	methods: {
		//test登录
		testfun: function() {
			uni.request({
				url: 'http://192.168.88.108:3000/singup/add',
				data: {
					mail:'xiaoyu@qq.com',
					name:'小鱼',
					pwd:'12345678'
				},
				method: 'POST',
				success: (data) => {
					console.log(data) 
				}
			})
		},
		//跳转到注册页面
		toSignUp: function() {
			uni.navigateTo({
				url: '../signup/signup',
			});
