		//获取用户名/邮箱
		getUser: function(e) {
			this.user = e.detail.value
		},
		//获取密码
		getPsw: function(e) {
			this.psw = e.detail.value
		},
		//登录提交
		login: function() {
			if (this.user && this.psw) {
				console.log(this.user, this.psw)
			}
		}
